<template>
    <div class="box">
        <div class="gwctit">
            购物车
        </div>
        <div class="con">
                <div class="contit">
                    <div>商品</div>
                    <div>价格</div>
                    <div>数量</div>
                </div>
                <div v-for="(a,$index) in $store.state.gwcarr" :key="$index">
                    <div>{{a.n}}</div>
                    <div>￥{{a.p}}</div>
                    <div>
                        <button @click="jian($index)">-</button>
                            {{a.num}}
                        <button @click="jia($index)">+</button>
                    </div>
                </div>
            <div class="zong">
               总价:{{zj}}
            </div>
        </div>
        
    </div>
</template>
<script>
import {mapGetters,mapActions} from 'vuex'
export default {
    data () {
        return {
            arr:[]
        }
    },
    computed:{
        ...mapGetters(['arr1']),
        zj(){
            let num = 0
            for(let i=0;i<this.$store.state.gwcarr.length;i++){
                num += this.$store.state.gwcarr[i].p*this.$store.state.gwcarr[i].num
                
            }
            return Math.ceil(num)
        }
    },
    created () {
        console.log(this.$store.state.gwcarr)
        if(localStorage.gwc){
            this.$store.state.gwcarr = [...JSON.parse(localStorage.gwc)]
        }
    },
    methods: {
        ...mapActions(['jia','jian']),
    },
}
</script>

<style scoped>
    .gwctit{
        text-align: center;
    }
    button{
        width: 20px;
        height: 20px;
    }
    .con>div{
        text-align: center;
    }
    .con>div>div{
        border: 1px solid #ddd;
        /* padding: 8px 50px; */
        width: 300px;
        height: 50px;
        /* height: 8px; */
        display: inline-block;
        line-height: 50px;
    }
    .contit>div{
        background-color: firebrick;
        color: #fff;
    }
    .zong{
        width: 900px;
        height: 50px;
        line-height: 50px;
        border: 1px solid #ddd;
        margin: 0 auto;
    }
</style>