import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

let state = {
  list:[
    {id:0,n:'苹果',t:'水果',p:39.9},
    {id:1,n:'香蕉',t:'水果',p:23},
    {id:2,n:'榴莲',t:'水果',p:399},
    {id:3,n:'牛排',t:'生鲜',p:9.9},
    {id:4,n:'羊排',t:'生鲜',p:98},
    {id:5,n:'三黄鸡',t:'生鲜',p:26},
    {id:6,n:'鲍鱼',t:'海鲜',p:5},
    {id:7,n:'鲈鱼',t:'海鲜',p:18},
    {id:8,n:'中华鲟',t:'海鲜',p:23},
    {id:9,n:'中华鲟',t:'海鲜',p:23},
    {id:10,n:'白菜',t:'蔬菜',p:23}
  ],
  arr2:[],
  gwcarr:[],
  index:0,
}

let getters = {
  arr1(state){
    let arr1 = ['全部']
    for(let i=0;i<state.list.length;i++){
      if(arr1.indexOf(state.list[i].t) == -1){
        arr1.push(state.list[i].t)
      }
    }
    return arr1
  },
}

let mutations = {
  m_arr1(state,type){
    state.index = type.num
    state.arr2 = []
    if(type.str == '全部'){
      state.arr2 = [...state.list]
    }
    for(let i=0;i<state.list.length;i++){
      if(type.str == state.list[i].t){
        state.arr2.push(state.list[i])
      }
    }
  },
  gwc(state,gwcdata){
      if(localStorage.gwc){
        state.gwcarr = [...JSON.parse(localStorage.gwc)]
      }
      if(state.gwcarr.length == 0){
        state.gwcarr.push({...gwcdata,num:0})
      }
      let type = true
      for(let i=0;i<state.gwcarr.length;i++){
          if(state.gwcarr[i].id == gwcdata.id){
            type = false
            state.gwcarr[i].num++
          }
      }
      if(type){
        state.gwcarr.push({...gwcdata,num:1})
      }
      localStorage.gwc = JSON.stringify(state.gwcarr)
  },
  jia(state,id){
    if(localStorage.gwc){
      state.gwcarr = [...JSON.parse(localStorage.gwc)]
    }
    state.gwcarr[id].num++
    
    localStorage.gwc = JSON.stringify(state.gwcarr)
  },
  jian(state,id){
    if(localStorage.gwc){
      state.gwcarr = [...JSON.parse(localStorage.gwc)]
    }
    state.gwcarr[id].num--
    if(state.gwcarr[id].num <= 0){
      state.gwcarr[id].num = 0
    }
    localStorage.gwc = JSON.stringify(state.gwcarr)
  }
  
}

let actions = {
  a_arr1({commit},type){
    commit('m_arr1',type)
  },
  gwc({commit},gwcdata){
    commit('gwc',gwcdata)
  },
  jia({commit},id){
    commit('jia',id)
  },
  jian({commit},id){
    commit('jian',id)
  }
}

export default new Vuex.Store({
  state,
  getters,
  mutations,
  actions
})